body {
  margin: 0;
  font-family: var(--pretendard);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--semantic-surface);
  color: var(--semantic-on-surface);
}

a {
  cursor: pointer;
}

img {
  width: 100%;
  border-radius: var(--scale-radius-3);
}

iframe {
  width: 100%;
  height: 400px;
  border-radius: var(--scale-radius-3);
}

/* 타이포 부품 */

.weight-regular {
  font-weight: var(--regular);
}

.weight-bold {
  font-weight: var(--bold);
}

.word {
  word-break: keep-all;
}

.lh-lg {
  line-height: var(--static-large);
}

.lh-md {
  line-height: var(--static-medium);
}

.lh-sm {
  line-height: var(--static-small);
}

/* 기본 타이포 셋팅 */

code {
  font-family: var(--sf-mono);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: var(--scale-dimension-size-0);
  line-height: var(--static-small);
  font-weight: var(--bold);
}

h1 {
  font-size: var(--scale-dimension-font-size-900);
}

h2 {
  font-size: var(--scale-dimension-font-size-800);
}

h3 {
  font-size: var(--scale-dimension-font-size-600);
}

h4 {
  font-size: var(--scale-dimension-font-size-500);
}

h5 {
  font-size: var(--scale-dimension-font-size-400);
}

h6 {
  font-size: var(--scale-dimension-font-size-300);
}

p {
  line-height: var(--static-medium);
  margin: var(--scale-dimension-size-0);
}

hr {
  width: 100%;
  height: var(--scale-dimension-size-10);
  background-color: var(--semantic-border-divider);
  border: 0;
}

.body-lg {
  font-size: var(--scale-dimension-font-size-200);
  line-height: var(--static-large);
  word-break: keep-all;
}

.body-md {
  font-size: var(--scale-dimension-font-size-100);
  line-height: var(--static-medium);
  word-break: keep-all;
}

.body-sm {
  font-size: var(--scale-dimension-font-size-50);
  line-height: var(--static-medium);
  word-break: keep-all;
}

.body-xsm {
  font-size: var(--scale-dimension-font-size-25);
  line-height: var(--static-medium);
  word-break: keep-all;
}

/* 기본 UI 스타일 셋팅  */

.links {
  text-decoration: none;
  color: var(--color-gray-800);
  cursor: pointer;
}

.links:hover {
  color: var(--color-gray-700);
}

.links-secondary {
  text-decoration: none;
  color: var(--color-gray-600);
  cursor: pointer;
}

.links-secondary:hover {
  color: var(--color-gray-700);
}

.post-list {
  border-top: 1px solid var(--semantic-border-divider);
}

.post-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--scale-dimension-size-1000);
  border-bottom: 1px solid var(--semantic-border-divider);
}

.post-information {
  display: flex;
}

.post-data {
  width: 10rem;
}

.post-category {
  width: 10rem;
}

.post-title {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

/* Post, Project Contnets */

.post-contents-title {
  margin-bottom: var(--scale-dimension-size-300);
}

.post-contents-title h4 {
  margin-bottom: var(--scale-dimension-size-25);
}

.post-contents-title p {
  color: var(--color-gray-500);
}

.post-contents img,
iframe {
  margin-bottom: var(--scale-dimension-size-300);
}

/* Post, Project UI Element */

@media (max-width: 520px) {
  .post-card {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    height: var(--scale-dimension-size-1500);
  }

  .post-information {
    font-size: var(--scale-dimension-font-size-75);
    margin-bottom: var(--scale-dimension-size-50);
  }

  .post-data {
    width: 4.5rem;
  }

  .post-category {
    width: 4rem;
  }
}

